.keypad{
    flex: 1.3;
    background-color: var(--bg-color-2);
    padding:20px;
    display: flex;
}

.keypad p{

    line-height: 1.875rem;
    font-size: 1.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--text-color-2);
    cursor: pointer;
    user-select: none;
    }

.keypad_keys{
    flex:3;
    display: flex;
    flex-wrap: wrap;
}

.keypad_keys P{
    width: 33%;
    height: 25%;
}
.keypad_keys p:active
{
    background-color: rgba(0,0,0,0.14);
}
.keypad_symbols{
    flex : 1;
}

.keypad_symbols p{
    color: #6b6b6b;
    width: 100%;
    height: 20%;
}